

























































import { Component, Vue } from 'vue-property-decorator';
import { Getter } from 'vuex-class';
import { Order, Table } from '../store/types';
import { MessageBox, Indicator } from 'mint-ui';
import api from '@/api/order';

@Component({
  components: {
  },
})
export default class Takeout extends Vue {
  @Getter('unusingTakeoutId')
  protected unusingTakeoutId: number | null;
  @Getter('usingTakeouts')
  protected takeouts: Table[];
  protected async doPayment(order: Order) {
    await MessageBox({
      $type: 'confirm',
      title: '',
      message: ' ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
    });
    Indicator.open('...');
    await api.completePayment(order.orderId);
    Indicator.close();
    await MessageBox({
      title: '',
      message: ' !',
      confirmButtonText: '',
    });
    this.$root.$emit('refresh');
  }
  protected async doSuccess(order: Table) {
    await MessageBox({
      $type: 'confirm',
      title: '',
      message: ' ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
    });
    Indicator.open('...');
    await api.exitOrder(order.orderInfoId);
    Indicator.close();
    await MessageBox({
      title: '',
      message: ' !',
      confirmButtonText: '',
    });
    this.$root.$emit('refresh');
  }
  protected async doCancel(order: Order) {
    await MessageBox({
      $type: 'confirm',
      title: '',
      message: ' ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
    });
    Indicator.open('...');
    await api.deleteOrder(order.orderId);
    Indicator.close();
    await MessageBox({
      title: '',
      message: ' !',
      confirmButtonText: '',
    });
    this.$root.$emit('refresh');
  }
}
