



















































































import { Component, Vue } from 'vue-property-decorator';
import { Getter } from 'vuex-class';
import { Order, Table } from '../store/types';
import { Indicator, MessageBox } from 'mint-ui';
import api from '@/api/order';
import moment from 'moment'

@Component({
  components: {
  },
})
export default class OrderComponent extends Vue {
  @Getter('tables')
  protected tables: Table[];
  @Getter('unusingTakeoutId')
  protected unusingTakeoutId: number | null;
  public remainDate (date: number) {
    return date && moment(date).add(90, 'm').format('H:m');
  }
  protected async doPayment(order: Order) {
    await MessageBox({
      $type: 'confirm',
      title: '',
      message: '  ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
    });
    Indicator.open('...');
    await api.completePayment(order.orderId);
    Indicator.close();
    await MessageBox({
      title: '',
      message: ' !',
      confirmButtonText: '',
    });
    this.$root.$emit('refresh');
  }
  protected async doDelete(order: Order) {
    await MessageBox({
      $type: 'confirm',
      title: '',
      message: ' ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
    });
    Indicator.open('...');
    await api.deleteOrder(order.orderId);
    Indicator.close();
    await MessageBox({
      title: '',
      message: ' !',
      confirmButtonText: '',
    });
    this.$root.$emit('refresh');
  }
  protected async exitOrder(table: Table) {
    await MessageBox({
      $type: 'confirm',
      title: '',
      message: ' ?',
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
    });
    Indicator.open('...');
    await api.exitOrder(table.orderInfoId);
    Indicator.close();
    await MessageBox({
      title: '',
      message: ' !',
      confirmButtonText: '',
    });
    this.$root.$emit('refresh');
  }
  get tableInfos() {
    return this.tables.map((table) => {
      return {
        ...table,
        orders: table.orders.map((order) => {
          return {
            ...order,
            totalPrice: order.orderProducts.reduce((result, data) => {
              return result + (data.productCount * data.product.salesAmount);
            }, 0),
          };
        }),
      };
    });
  }
}
